.card-list {
  @include margin;
  @include verticalCenter;
  flex-wrap: wrap;
  row-gap: 50px;
  column-gap: 60px;
  padding: 55px 10px;
  background-color: $background-1;
  border-radius: 25px;
  margin-top: 45px;
  margin-bottom: 50px;
  position: relative;

  @include tablet {
    margin: 35px 50px 0 50px;
    justify-content: space-between;
  padding: 25px 10px;
  row-gap: 20px;
  column-gap: 15px;
   
  }

  @include mobile {
    margin: 22px 20px 25px 20px;
    background-color: $color-2;
    padding: 0;
    row-gap: 20px;
  }

  .card {
    display: flex;
    width: 28%;
    flex-direction: column;
    cursor: pointer;
    position: relative;

    @include tablet {
      width: 48%;
    }

    @include mobile {
      width: 100%;
    }

    &:after {
      position: absolute;
      content: '';
      width: 100%;
      height: 98%;
      top: 0;
      left: 0;
      background: $color-1;
      opacity: 0;
      transition: 0.5s;
      pointer-events: none;
    }

    &:hover:after {
      opacity: 0.2;
      border-radius: 10px;
    }
  }

  img {
    height: 340px;
    width: 100%;
    object-fit: cover;
    border-radius: 10px;
    filter: brightness(90%);

    @include mobile {
      height: 255px;
    }
  }

  p {
    color: $color-2;
    position: absolute;
    bottom: 20px;
    left: 20px;
    right: 20px;
    z-index: 1;
    width: 200px;
  }
}