footer {
  @include verticalCenter;
  flex-direction: column;
  background: $background-2;
  color: $color-2;
  width: 100%;

  img {
    margin-top: 65px;

    @include tablet {
      margin-top: 50px;
    }
  }

  p {
    margin: 30px 0;
    font-size: 24px;

    @include tablet {
      font-size: 17px;
      margin-top: 18px;
    }

    @include mobile {
      font-size: 12px;
      margin: 10px 0 60px;
      width: 130px;
      text-align: center;

    }
  }
}