.carrousel_container {
  position: relative;
  height: 415px;

  @include tablet {
    height: 315px;
  }

  @include mobile {
    height: 255px;
  }

  img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 25px;

    @include tablet {
      border-radius: 15px;
    }

    @include mobile {
      border-radius: 10px;
    }
  }

  .pagination {
    position: absolute;
    bottom: 25px;
    left: 50%;
    transform: translateX(-50%);
    color: white;

    @include tablet {
      font-size: 15px;
      bottom: 15px;
    }

    @include mobile {
      display: none;
    }
  }

  .arrow_left {
    position: absolute;
    top: 50%;
    left: 25px;
    transform: translateY(-50%);
    width: 45px;
    height: 80px;
    cursor: pointer;

    @include tablet {
      width: 35px;
      height: 60px;
      left: 10px;
    }

    @include mobile {
      width: 25px;
      height: 45px;
      left: 5px;
    }
  }

  .arrow_right {
    position: absolute;
    top: 50%;
    right: 25px;
    transform: translateY(-50%);
    width: 45px;
    height: 80px;
    cursor: pointer;

    @include tablet {
      width: 35px;
      height: 60px;
      right: 10px;
    }

    @include mobile {
      width: 25px;
      height: 45px;
      right: 5px;
    }
  }
}